.contact-links {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 2rem;
}

.contact-links a {
    font-size: 1.5rem;
    margin: 2rem 2rem;
    padding: 0.5rem 1rem;
    border: 1px solid var(--border-color);
    border-radius: 10px;
    transition: all 0.3s ease;
}

.contact-links a:hover {
    background-color: var(--accent-color);
    border-color: var(--accent-border-color);
    transform: translateY(-2px) scale(101%);
}
